import React, { Component } from "react";
import { FaGithub } from "react-icons/fa";
import { FooterS } from "./styles";
import "./globals";
class footer extends Component {
	state = {};
	render() {
		return (
			<>
				<FooterS>
					<div>
						<a href='https://github.com/Daviiro/'>
							<FaGithub size='40' />
						</a>
					</div>
					<div>
						<span>Copyright © 2020 Daviiro. None rights reserved.</span>
					</div>
				</FooterS>
			</>
		);
	}
}

export default footer;
